<template>
  <el-container class="home-container">
    <!--  -->
    <el-header>
      <div>
        <img src="../assets/logo.png" alt />
        <span>Admin</span>
      </div>
      <el-dropdown>
        <span class="el-dropdown-link">
          {{ loginname.name }}
          <i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="updatePasword = true"
            ></el-dropdown-item
          >
          <el-dropdown-item @click.native="logout()"></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <!--  -->
    <el-container>
      <!--  -->
      <el-aside :width="isCollapse ? '65px' : '200px'">
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <el-menu
          background-color="#333744"
          text-color="#fff"
          active-text-color="#409EFF"
          :unique-opened="true"
          :collapse="isCollapse"
          :collapse-transition="false"
          :router="true"
          :default-active="activePath"
        >
          <!--  -->
          <el-submenu
            v-for="item in menulist"
            :key="item.id"
            :index="item.id + ''"
          >
            <template slot="title">
              <i :class="item.icon"></i>
              <span class="sider-bar-title">{{ item.name }}</span>
            </template>
            <!--  -->
            <el-menu-item
              v-for="subItem in item.childMenu"
              :key="subItem.id"
              :index="subItem.url"
              @click="saveNavState(subItem.url)"
            >
              <template slot="title">
                <i :class="subItem.icon"></i>
                <span class="sider-bar-title">{{ subItem.name }}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!--  -->
      <el-main>
        <!-- -->
        <router-view />
        <!-- -->
        <el-dialog
          title=""
          :visible.sync="updatePasword"
          width="50%"
          @close="updatePaswordClose"
        >
          <!-- -->
          <el-form
            ref="updatePaswordFormRef"
            :model="updatePaswordUser"
            :rules="updatePaswordRulesForm"
            label-width="90px"
          >
            <el-form-item label="" prop="password">
              <el-input
                v-model="updatePaswordUser.password"
                type="password"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="newPassword">
              <el-input
                v-model="updatePaswordUser.newPassword"
                type="password"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="checkPassword">
              <el-input
                v-model="updatePaswordUser.checkPassword"
                type="password"
              ></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="updatePasword = false"> </el-button>
            <el-button type="primary" @click.native="updatePaswordMethod()"
              > </el-button
            >
          </span>
        </el-dialog>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      menulist: [],
      isCollapse: false,
      // 
      activePath: '',
      loginname: {
        name: ''
      },
      updatePasword: false,
      updatePaswordUser: {
        name: [],
        password: [],
        newPassword: [],
        checkPassword: []
      },
      updatePaswordRulesForm: {
        password: [{ required: true, message: '', trigger: 'blur' }],
        newPassword: [{ required: true, message: '', trigger: 'blur' }],
        checkPassword: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getMenuList()
    this.activePath = window.sessionStorage.getItem('activePath')
    this.loginname.name = window.sessionStorage.getItem('loginName')
  },
  methods: {
    logout() {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    // 
    async getMenuList() {
      const { data: res } = await this.$api.menu.findNavTree()
      if (res.code === 200) {
        return (this.menulist = res.data)
      } else if (res.code === 20003) {
        this.$message.error('')
        return this.$router.push('/login')
      }
    },
    // 
    toggleCollapse() {
      this.isCollapse = !this.isCollapse
    },
    // 
    saveNavState(activePath) {
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
    },
    updatePaswordClose() {
      this.$refs.updatePaswordFormRef.resetFields()
    },
    updatePaswordMethod() {
      this.$refs.updatePaswordFormRef.validate(async valid => {
        if (!valid) return
        this.updatePaswordUser.name = window.sessionStorage.getItem('loginName')
        if (
          this.updatePaswordUser.newPassword !==
          this.updatePaswordUser.checkPassword
        )
          return this.$message.error('')
        // 
        const { data: res } = await this.$api.user.updatepassword(
          this.updatePaswordUser
        )
        if (res.code !== 200) {
          this.$message.error('')
        }
        this.$message.success('')
        window.sessionStorage.clear()
        this.$router.push('/login')
      })
    }
  }
}
</script>
