<template>
  <div>
    <!-- -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!-- -->
    <el-card>
      <!-- -->
      <el-row :gutter="30">
        <el-col :span="5">
          <el-input
            v-model="queryInfo.mes"
            placeholder="///"
            clearable
            @clear="getUserList"
          ></el-input>
        </el-col>
        <el-col :span="5">
          <el-select v-model="queryInfo.status" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="5">
          <el-date-picker
            v-model="timeValue"
            type="datetimerange"
            align="right"
            start-placeholder=""
            end-placeholder=""
            value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-button type="primary" plain @click="getUserList"></el-button>
          <el-button plain @click="exportData"></el-button>
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!-- -->
      <el-table border :data="userList">
        <template v-for="(item, index) in tableHead">
          <el-table-column
            v-if="item.column_name"
            :key="index"
            :prop="item.column_name"
            :label="item.column_comment"
          ></el-table-column>
        </template>
        <el-table-column label="" width="50px">
          <template slot-scope="scope">
            <p v-if="scope.row.status === 1"></p>
            <p v-else-if="scope.row.status === 0"></p>
          </template>
        </el-table-column>
        <el-table-column label="" width="120px">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeUserById(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- -->
      <el-pagination
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </el-card>
    <!-- -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!-- -->
      <el-form
        ref="addFormRef"
        :model="createUser"
        :rules="addRulesForm"
        label-width="70px"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="createUser.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="createUser.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="nickName">
          <el-input v-model="createUser.nickName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="createUser.mobile"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="createUser.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <template>
            <el-radio v-model="createUser.status" label="0"></el-radio>
            <el-radio v-model="createUser.status" label="1"></el-radio>
          </template>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>
    <!-- -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        ref="editFormRef"
        :model="editForm"
        :rules="addRulesForm"
        label-width="70px"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="nickName">
          <el-input v-model="editForm.nickName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <template>
            <el-radio v-model="editForm.status" label="0"></el-radio>
            <el-radio v-model="editForm.status" label="1"></el-radio>
          </template>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
// import { formTime } from '../../vendor/FormTime'
export default {
  data() {
    // 
    var checkEmail = (rule, value, cb) => {
      const regEmail = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      if (regEmail.test(value)) {
        return cb()
      }
      cb(new Error(''))
    }
    // 
    var checkMobile = (rule, value, cb) => {
      const regMobile = /^1[3456789]\d{9}$/
      if (regMobile.test(value)) {
        return cb()
      }
      cb(new Error(''))
    }
    return {
      timeValue: '',
      // 
      queryInfo: {
        mes: '',
        status: '',
        startTime: '',
        endTime: '',
        pageNum: 1,
        pageSize: 10
      },
      total: 0,
      tableHead: [
        { column_name: 'id', column_comment: 'ID' },
        { column_name: 'name', column_comment: '' },
        { column_name: 'nickName', column_comment: '' },
        { column_name: 'mobile', column_comment: '' },
        { column_name: 'email', column_comment: '' },
        { column_name: 'createTime', column_comment: '' },
        { column_name: 'updateTime', column_comment: '' },
        { column_name: 'lastUpdateBy', column_comment: '' }
      ],
      userList: [],
      // 
      addDialogVisible: false,
      // 
      createUser: {
        name: [],
        password: [],
        nickName: [],
        mobile: [],
        email: [],
        status: '0'
      },
      // 
      addRulesForm: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }],
        nickName: [{ required: true, message: '', trigger: 'blur' }],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        status: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      editDialogVisible: false,
      editForm: {
        // 
        lastUpdateBy: []
      },
      options: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        },
        {
          value: '',
          label: ''
        }
      ]
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    async getUserList() {
      this.queryInfo.startTime = this.timeValue[0]
      this.queryInfo.endTime = this.timeValue[1]
      const { data: userRes } = await this.$api.user.findPage(this.queryInfo)
      if (userRes.code !== 200) {
        return this.$message.error('')
      }
      this.userList = userRes.data.users
      this.total = userRes.data.total
    },
    // pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getUserList()
    },
    //  
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getUserList()
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // 
        // const { data: res } = await this.$http.post(
        //   '/user/create',
        //   this.createUser
        // )
        const { data: res } = await this.$api.user.create(this.createUser)
        if (res.code !== 200) {
          this.$message.error('')
        }
        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getUserList()
      })
    },
    // 
    async showEditDialog(id) {
      // const { data: res } = await this.$http.get('/user/getuserbyid/' + id)
      const { data: res } = await this.$api.user.getuserbyid(id)
      if (res.code !== 200) {
        return this.$message.error('')
      }

      this.editForm = res.data
      this.editDialogVisible = true
    },
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editUserInfo() {
      this.editForm.lastUpdateBy = window.sessionStorage.getItem('token')
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$api.user.update(this.editForm)
        if (res.code !== 200) {
          return this.$message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        // 
        this.$message.success('')
      })
    },
    // id
    async removeUserById(id) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      // const { data: res } = await this.$http.delete('/user/deleteuser/' + id)
      const { data: res } = await this.$api.user.deleteuser(id)
      if (res.code !== 200) {
        return this.$message.error('')
      }
      // 
      this.$message.success('')
      // 
      this.getUserList()
    },
    // 
    exportData() {
      require.ensure([], () => {
        // 
        // const { export_json_to_excel } = require('@/vendor/Export2Excel')
        const { exportJsonToExcel } = require('../../vendor/Export2Excel')
        // 
        const tHeader = [
          'id',
          '',
          '',
          '',
          '',
          '',
          '',
          ''
        ]
        // ,  list 
        const filterVal = [
          'id',
          'name',
          'nickName',
          'mobile',
          'email',
          'createTime',
          'updateTime',
          'lastUpdateBy'
        ]
        // 
        const list = this.userList
        const data = this.formatJson(filterVal, list)
        const time = this.$moment(new Date()).format('YYYY-MM-DD')
        // fileName: 
        exportJsonToExcel(tHeader, data, 'UsersList' + time)
      })
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    }
  }
}
</script>

<style lang="less" scoped></style>
