// 
export const login = data => {
  return reques({
    url: 'http://10.20.19.38:10080/user/login',
    method: 'post',
    data
  })
}

// 
export const logout = () => {
  return reques({
    url: 'http://10.20.19.38:10080/user/logout',
    method: 'get'
  })
}
