<template>
  <div>
    <!-- -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!-- -->
    <el-card>
      <!-- -->
      <el-row>
        <el-col :span="6">
          <!-- <el-button type="primary" plain @click="getStudentList"
            ></el-button
          > -->
          <el-button type="primary" @click="exportData"></el-button>
          <!-- <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          > -->
        </el-col>
      </el-row>
      <!-- -->
      <el-table border :data="toDayList">
        <el-table-column type="index" width="50" label=""></el-table-column>
        <template v-for="(item, index) in tableHead">
          <el-table-column
            v-if="item.column_name"
            :key="index"
            :prop="item.column_name"
            :label="item.column_comment"
          ></el-table-column>
        </template>
      </el-table>
      <!-- -->
      <el-pagination
        :current-page="toDayList.pagenum"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="toDayList.pagesize"
        layout="sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      findToDayBody: {
        pageNum: 1,
        pageSize: 10
      },
      total: 0,
      tableHead: [
        { column_name: 'course_name', column_comment: '' },
        { column_name: 'content_title', column_comment: '' },
        { column_name: 'start_time', column_comment: '' },
        { column_name: 'school_name', column_comment: '' },
        { column_name: 'student_number', column_comment: '' },
        { column_name: 'callback_key', column_comment: 'CC_room_id' },
        { column_name: 'teacher_info', column_comment: '' }
      ],
      toDayList: []
    }
  },
  // 
  created() {
    this.getToDayList()
  },
  methods: {
    async getToDayList() {
      const { data: userRes } = await this.$api.course.findToDayList(
        this.findToDayBody
      )
      if (userRes.code !== 1) {
        return this.$message.error('')
      }
      this.toDayList = userRes.data
    },
    // 
    exportData() {
      require.ensure([], () => {
        const { exportJsonToExcel } = require('../../vendor/Export2Excel')
        // 
        const tHeader = [
          '',
          '',
          '',
          '',
          '',
          'CC_room_id',
          ''
        ]
        // ,  list 
        const filterVal = [
          'course_name',
          'content_title',
          'start_time',
          'school_name',
          'student_number',
          'callback_key',
          'teacher_info'
        ]
        // 
        const list = this.toDayList
        const data = this.formatJson(filterVal, list)
        const time = this.$moment(new Date()).format('YYYY-MM-DD')
        // fileName: 
        exportJsonToExcel(tHeader, data, '' + time)
      })
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    }
  }
}
</script>

<style lang="less" scoped></style>
