          prop="remark"
        ></el-table-column>
        <el-table-column label="" width="80px" prop="jobCron">
        </el-table-column>
        <el-table-column label="" width="80px">
          <template slot-scope="scope">
            <div class="apiStatus">
              <font v-if="scope.row.enabled" color="#67C23A" class="apiActive">
                
              </font>
              <font v-else color="#F56C6C" class="apiNoActive">
                
              </font>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                ricon="el-icon-edit"
                circle
                @click="showEditDialog(scope.row.jobId)"
              ></el-button>
            </el-tooltip>
            <!-- / -->
            <el-tooltip
              v-if="scope.row.enabled === 1"
              class="item"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button
                type="danger"
                icon="el-icon-switch-button"
                size="mini"
                circle
                @click="enableMonitor(scope)"
              ></el-button>
            </el-tooltip>

            <el-tooltip
              v-else
              class="item"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button
                type="success"
                icon="el-icon-caret-right"
                size="mini"
                circle
                @click="enableMonitor(scope)"
              ></el-button>
            </el-tooltip>

            <!--  -->
            <el-tooltip
              class="item"
