          </template>
        </el-table-column>
      </el-table>
      <!-- -->
      <el-pagination
        :current-page="findMonitorBody.pageNum"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="findMonitorBody.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </el-card>

    <!-- API--->
    <el-dialog
      title="API"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!-- form-->
      <el-form
        ref="addApiFormRef"
        :model="createApi"
        :rules="addRulesMonitorForm"
        label-width="100px"
      >
        <el-form-item label="" prop="httpSystemId">
          <el-select
            v-model="createApi.systemName"
            filterable
            allow-create
            default-first-option
            placeholder=""
          >
            <el-option
              v-for="item in groupsList"
              :key="item.index"
              :label="item.systemName"
              :value="item.systemName"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="job" prop="jobName">
          <el-input v-model="createApi.jobName"></el-input>
