        <el-form-item label="api" prop="httpName">
          <el-input v-model="createApi.httpName"></el-input>
        </el-form-item>
        <el-form-item label="API" prop="httpMethod">
          <template>
            <el-radio v-model="radioApiType" label="0">GET</el-radio>
